.nav {
  padding-top: 30px;
  text-align: center;

  @include vp-767 {
    padding: 20px 0 14px;
  }
}

.nav__main-nav {
  padding-bottom: 12px;
  border-bottom: 1px solid $color-seashell;

  @include vp-1023 {
    padding-bottom: 22px;
    border-bottom: none;
  }

  @include vp-767 {
    padding: 0;
  }
}

.nav__container {
  display: grid;
  grid-template-columns: 1fr 166px 1fr;

  &--login {
    grid-template-columns: 1fr;
  }
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav__logo {
  padding: 0 50px;
}

.nav__toggle {
  display: none;
  margin-top: 12px;
  margin-left: -8px;
  width: 40px;
  height: 40px;
  text-align: start;
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: $color-transparent;
  position: relative;

  @include vp-1023 {
    display: block;

    &::before {
      position: absolute;
      content: "";
      width: 25px;
      height: 18px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  @include vp-767 {
    margin-top: 6px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      opacity: 0.6;
    }
  }

  &:active {
    opacity: 0.3;
  }
}

.nav__search {
  padding: 5px 0 26px;
  text-align: start;

  @include vp-1023 {
    padding: 35px 0 25px;
    order: 10;
    grid-column: 1 / 4;
    width: 100%;
  }

  @include vp-767 {
    padding: 25px 0;
    margin-bottom: 10px;
  }
}

.nav__user-nav {
  padding: 12px 0 25px;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;

  @include vp-1023 {
    padding: 0;
  }
}

.nav__user-link {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  margin-left: 35px;
  color: $color-wood-bark;
  padding-bottom: 10px;

  &--login {
    @include vp-1023 {
      display: none;
    }
  }

  &--cart {
    @include vp-1023 {
      margin-top: 18px;
      margin-right: -7px;
      width: 30px;
      height: 30px;
      padding-top: 0;
      position: relative;

      &::before {
        position: absolute;
        content: "";
        width: 20px;
        height: 18px;
        background: url("../img/svg/cart.svg");
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }

    @include vp-767 {
      margin-top: 11px;
    }
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      opacity: 0.6;
    }
  }

  &:active {
    opacity: 0.3;
  }

  &--cart span {
    @include vp-1023 {
      display: none;
    }
  }

  &--cart b {
    margin-left: 6px;
    font-weight: 500;
  }
}

.nav__product-nav {
  padding: 18px 0 19px 11px;

  @include vp-1023 {
    padding: 0;
  }
}

.nav__company-nav {
  display: none;
}

.nav__link {
  display: none;
  text-align: start;
  font-size: 18px;
  line-height: 23px;
  color: $color-default-white;

  @include vp-1023 {
    padding-bottom: 30px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      opacity: 0.6;
    }
  }

  &:active {
    opacity: 0.3;
  }
}

.is-closed {
  .nav__search {
    @include vp-1023 {
      display: none;
    }
  }

  .nav__toggle {
    @include vp-1023 {
      display: block;

      &::before {
        background: url("../img/svg/menu-open.svg");
      }
    }
  }

  .nav__product-nav {
    @include vp-1023 {
      display: none;
    }
  }

  .nav__company-nav {
    display: none;
  }
}

.is-opened {
  @include vp-1023 {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    overflow-y: scroll;
    background-color: $color-medium-wood;
  }

  .nav__toggle {
    @include vp-1023 {
      display: block;

      &::before {
        background: url("../img/svg/menu-close.svg");
      }
    }
  }

  svg {
    @include vp-1023 {
      fill: $color-default-white;
    }
  }

  .nav__user-link--cart {
    @include vp-1023 {
      &::before {
        background: url("../img/svg/cart-mobile-menu.svg");
      }
    }
  }

  .nav__search {
    @include vp-1023 {
      display: block;
    }
  }

  .nav__product-nav {
    @include vp-1023 {
      display: block;
    }
  }

  .nav__company-nav {
    @include vp-1023 {
      display: block;
    }
  }

  .nav__link {
    @include vp-1023 {
      display: block;
    }
  }
}

.is-nojs {
  @include vp-1023 {
    background-color: $color-medium-wood;
  }

  .nav__toggle {
    @include vp-1023 {
      display: none;
    }
  }

  .nav__logo {
    @include vp-1023 {
      grid-column: 2 / 3;
    }
  }

  svg {
    @include vp-1023 {
      fill: $color-default-white;
    }
  }

  .nav__search {
    @include vp-1023 {
      display: block;
    }
  }

  .nav__user-link--cart {
    @include vp-1023 {
      &::before {
        background: url("../img/svg/cart-mobile-menu.svg");
      }
    }
  }

  .nav__product-nav {
    @include vp-1023 {
      display: block;
    }
  }

  .nav__company-nav {
    @include vp-1023 {
      display: block;
    }
  }

  .nav__link {
    @include vp-1023 {
      display: block;
    }
  }
}
