.is-show {
  display: block; }

.form-login h2 {
  margin: 0;
  margin-bottom: 23px;
  font-family: "Cormorant Garamond", "Times New Roman", serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  text-align: center; }
  @media (max-width: 767px) {
    .form-login h2 {
      margin-bottom: 17px;
      font-size: 24px;
      line-height: 31px; } }

.form-login__field--login {
  margin-bottom: 25px; }
  @media (max-width: 767px) {
    .form-login__field--login {
      margin-bottom: 20px; } }

.form-login__field--password {
  margin-bottom: 5px; }

.form-login__field input {
  width: 100%;
  min-height: 40px;
  padding: 0;
  background: #f0eae4;
  border: none;
  border-bottom: 1px solid #1d1613;
  font-size: 14px;
  line-height: 21px; }
  .form-login__field input::placeholder {
    color: #9b9997; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .form-login__field input:hover {
      opacity: 0.6; } }
  .form-login__field input:active {
    opacity: 0.3; }

.form-login__restore-link {
  margin-bottom: 45px;
  display: block;
  text-align: end;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #9b9997; }
  @media (max-width: 767px) {
    .form-login__restore-link {
      margin-bottom: 35px; } }
  @media (hover: hover), screen and (min-width: 0\0) {
    .form-login__restore-link:hover {
      opacity: 0.6; } }
  .form-login__restore-link:active {
    opacity: 0.3; }

.form-login__button {
  width: 100%;
  min-height: 68px;
  margin-bottom: 15px;
  background-color: #a87b62;
  border: none;
  color: #ffffff;
  cursor: pointer; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .form-login__button:hover {
      opacity: 0.6; } }
  .form-login__button:active {
    opacity: 0.3; }

.modal-success {
  z-index: 1000;
  position: fixed;
  left: 100px;
  top: 200px;
  right: 100px;
  padding: 10px 3px;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  border: 5px solid #a87b62;
  background-color: #f0eae4; }
  @media (max-width: 1023px) {
    .modal-success {
      top: 300px; } }
  @media (max-width: 767px) {
    .modal-success {
      top: 350px;
      left: 10%;
      right: 10%;
      font-size: 24px; } }

.login {
  background-color: #f0eae4; }

.login__image {
  width: 100%; }

.login__image img {
  width: 100%;
  max-height: 260px;
  object-fit: cover; }

.login__form {
  padding: 100px 200px; }
  @media (max-width: 1023px) {
    .login__form {
      padding: 100px 100px; } }
  @media (max-width: 767px) {
    .login__form {
      padding: 50px 0; } }

.login__registration {
  width: 229px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .login__registration {
      width: 224px; } }

.login__registration span {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9b9997; }

.login__registration-link {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .login__registration-link:hover {
      opacity: 0.6; } }
  .login__registration-link:active {
    opacity: 0.3; }

